import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty";
import Json from '@/Json';
import axios from '@/utils/uniAxios.js'
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			src:'../static/nogoods.png',
			msg:'你还没有任何订单，看看其他的吧',
			tabCurrentIndex: 0,
			navList: [{
					status: '',
					text: '全部',
					loadingType: 'more',
					orderList: []
				},
				{
					status: 0,
					text: '待付款',
					loadingType: 'more',
					orderList: []
				},
				{
					status: 1,
					text: '待发货',
					loadingType: 'more',
					orderList: []
				},
				{
					status: 2,
					text: '待收货',
					loadingType: 'more',
					orderList: []
				},
				{
					status: 3,
					text: '待评价',
					loadingType: 'more',
					orderList: []
				},
			],
			page:{
				current:1,
				pageSize:10
			},
			orderList:[],
			payCode:{
				appId: "",
				nonceStr: "",
				package: "",
				paySign: "",
				signType: "MD5",
				timeStamp: ""
			}
		};
	},

	onLoad(options) {
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		console.log(options)
		this.tabCurrentIndex = +options.state;
		if(options.state===''){//全部订单
			this.loadData('tabChange','');
		}else if(options.state===0){//待付款
			this.loadData('tabChange',options.state);
		}else{
			this.loadData('tabChange',options.state);
		}
	},

	methods: {
		//获取订单列表
		loadData(source,n) {
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.status;//每个状态的值
			if (source === 'tabChange' && navItem.loaded === true) {
				//tab切换只有第一次需要加载数据
				return;
			}
			if (navItem.loadingType === 'loading') {
				//防止重复加载
				return;
			}

			// navItem.loadingType = 'loading';
			let obj = {
			  "orderType": 0,//订单类型
			  "pageNum": this.page.current,//页码
			  "pageSize": this.page.pageSize,//页数
			  "sourceType":1,//订单来源
			  "status": n//订单状态：0->待付款；1->待发货；2->已发货(待收货)；3->已完成(待评价)；4->已关闭；5->无效订单
			}

			axios.post('/order/list',obj).then(res=>{
				if(res.data.code=='200'){
					let result = res.data.data.list;
					this.orderList = result.filter(item => {
						//添加不同状态下订单的表现形式
						item = Object.assign(item, this.orderStateExp(item.status));
						if (state === '') {
							//0为全部订单
							return item;
						}
						return item.status === state
					});
					
					this.orderList.forEach(item => {
						navItem.orderList.push(item);
					})
					this.$set(navItem, 'loaded', true);
				}
			})
			// let orderList = Json.orderList.filter(item => {
			// 	//添加不同状态下订单的表现形式
			// 	item = Object.assign(item, this.orderStateExp(item.state));
			// 	//演示数据所以自己进行状态筛选
			// 	if (state === 0) {
			// 		//0为全部订单
			// 		return item;
			// 	}
			// 	return item.state === state
			// });
			
			// orderList.forEach(item => {
			// 	navItem.orderList.push(item);
			// })
			//loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
			

			//判断是否还有数据， 有改为 more， 没有改为noMore 
			// navItem.loadingType = 'more';
		},

		//swiper 切换
		changeTab(e) {
			if(e.target){//在本页面点击tab切换
				this.tabCurrentIndex = e.target.current;
				if(this.tabCurrentIndex==0){//全部
					this.loadData('tabChange','');
				}else{
					this.loadData('tabChange',this.tabCurrentIndex-1);
				}
			}else{//从我的页面进入
				this.loadData('tabChange',this.tabCurrentIndex-1);
			}
			
			
		},
		//顶部tab点击
		tabClick(index) {
			this.tabCurrentIndex = index;
			console.log(this.tabCurrentIndex,888)
		},
		// 评价
		evaluate(item) {
			console.log(item)
		},
		// 再次购买
		againBuy(item) {
			let id = item.id;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
		//取消订单
		cancelOrder(item) {
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() => {
				let {
					stateTip,
					stateTipColor
				} = this.orderStateExp(9);
				item = Object.assign(item, {
					state: 9,
					stateTip,
					stateTipColor
				})

				//取消订单后删除待付款中该项
				let list = this.navList[1].orderList;
				let index = list.findIndex(val => val.id === item.id);
				index !== -1 && list.splice(index, 1);

				uni.hideLoading();
			}, 600)
		},

		//订单状态文字和颜色
		orderStateExp(state) {
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch (+state) {
				case 0:
					stateTip = '待支付';
					break;
				case 1:
					stateTip = '待发货';
					break;
				case 2:
					stateTip = '待收货';
					break;
				case 3:
					stateTip = '待评价';
					break;
				case 5:
					stateTip = '';
					break;
				case 6:
					stateTip = '交易成功';
					break;
				case 7:
					stateTip = '已完成';
					break;
				case 9:
					stateTip = '订单已关闭';
					stateTipColor = '#909399';
					break;
			}
			return {
				stateTip,
				stateTipColor
			};
		},
		//订单详情
		orderDetails(item,id) {
			console.log(item)
			if(item.status!=3){
				uni.navigateTo({
					url: 'orderDetails?status=' + item.status+'&id='+id
				})
			}
			
		},
		// 物流信息
		logisticsTap(){
			uni.navigateTo({
				url:'logistics'
			})
		},
		payGoods(item){
			let ids = [item.id];
			console.log(item)
			uni.navigateTo({
				url:'../shopcar/postOrder?deleIds='+JSON.stringify(ids)
			})
		}
	},
}
